.admin-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.admin-container h1 {
  margin-bottom: 20px;
  color: #333;
}

.filter-controls {
  margin-bottom: 20px;
}

.filter-select {
  margin-left: 10px;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.admin-layout {
  display: flex;
  gap: 20px;
  height: calc(100vh - 200px);
}

.conversation-list {
  flex: 1;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  overflow-y: auto;
  background-color: #f9f9f9;
  max-width: 400px;
}

.conversation-list h2 {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.conversation-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.conversation-list li {
  padding: 10px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  transition: background-color 0.2s;
}

.conversation-list li:hover {
  background-color: #f0f0f0;
}

.conversation-list li.selected {
  background-color: #e6f7ff;
  border-left: 3px solid #1890ff;
}

.conversation-item {
  display: flex;
  flex-direction: column;
}

.chat-type {
  display: inline-block;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: bold;
  margin-right: 10px;
  color: white;
}

.chat-type.general {
  background-color: #52c41a;
}

.chat-type.brokerage {
  background-color: #1890ff;
}

.timestamp {
  font-size: 0.8rem;
  color: #888;
  margin-bottom: 5px;
}

.message-count {
  font-size: 0.8rem;
  color: #666;
  margin-bottom: 5px;
}

.last-message {
  font-size: 0.9rem;
  color: #333;
  margin: 5px 0 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.conversation-details {
  flex: 2;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  overflow-y: auto;
  background-color: #fff;
}

.conversation-details h2 {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.conversation-header {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.conversation-header p {
  margin: 5px 0;
}

.message-history {
  padding: 10px;
}

.message-history h3 {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.1rem;
}

.admin-message {
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 8px;
  position: relative;
}

.admin-message.user {
  background-color: #f0f7ff;
  border-left: 3px solid #1890ff;
}

.admin-message.assistant {
  background-color: #f6ffed;
  border-left: 3px solid #52c41a;
}

.message-role {
  font-size: 0.8rem;
  font-weight: bold;
  margin-bottom: 5px;
  color: #666;
}

.admin-message.user .message-role {
  color: #1890ff;
}

.admin-message.assistant .message-role {
  color: #52c41a;
}

.error {
  color: #f5222d;
  padding: 10px;
  background-color: #fff1f0;
  border-radius: 4px;
  border: 1px solid #ffccc7;
}
